
.contact .container {
  padding: 30px 20px;
  position: relative;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.7);
}
.contact .container:after {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.6;
    background: url("../../assets/aboutme/bg.jpg");
    background-position: center;
    background-size: cover;
}
@media screen and (min-width: 760px) {
.contact .container {
      padding: 120px;
}
}
.contact .inner {
  height: 100%;
  width: 290px;
  color: #fff;
  text-align: left;
}
@media screen and (min-width: 760px) {
.contact .inner {
      width: 90%;
}
}
.contact .inner h1, .contact .inner h2, .contact .inner h4 {
    font-weight: bold;
    font-size: 44px;
    padding: 10px 0;
}
.contact .inner h2 {
    font-size: 28px;
}
.contact .inner h4 {
    font-size: 22px;
    font-weight: normal;
}
.contact .inner p {
    font-size: 22px;
    padding: 32px 0;
    line-height: 1.0;
}
.contact .inner input, .contact .inner textarea {
    font-size: 1.5em;
    width: 100%;
    padding: 0.9em;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    margin-bottom: 0.8em;
}
.contact .inner textarea {
    overflow: auto;
    height: 200px;
    resize: none;
}
.contact .inner button {
    font-size: 22px;
    background: white;
    padding: 20px;
    border: 3px solid rgba(0, 214, 90, 0.6);
}
@media screen and (min-width: 1300px) {
.contact .inner {
      width: 960px;
      margin: 0 auto;
}
}



